<template>
<div class="content-wrapper">
    <div class="container">
        <div class="login-box">
            <div class="login-box-body">
                <p v-if="!isSendCode" class="login-box-msg">Quên mật khẩu</p>
                <form v-if="!isSendCode" v-on:submit.prevent="sendCode" action="javascript:void(0)">
                    <div class="form-group has-feedback">
                        <input v-on:keyup="checkEmail" v-model="user.email" name="email" v-validate="'required|email'" type="email" class="form-control" placeholder="Email">
                        <span class="glyphicon glyphicon-envelope form-control-feedback"></span>
                        <span class="error">{{ errors.first("email") }}</span>
                        <span v-if="isEmail==false" class="error">Email này chưa đăng ký !</span>
                        <span v-if="isEmail==true" style="color:#8BC34A;">Email hơp lệ, bấm gửi code !</span>
                    </div>
                    
                    <div class="row">
                        <div class="col-xs-8"></div>
                        <div class="col-xs-4">
                            <button v-if="isEmail" type="submit" class="btn btn-primary btn-block btn-flat">Gửi code</button>
                        </div>
                    </div>
                </form>

                <p v-if="isSendCode" class="login-box-msg">Nhập code</p>
                <form v-if="isSendCode" v-on:submit.prevent="forgotPassword" action="javascript:void(0)" >
                    <div class="form-group has-feedback">
                        <input v-model="user.code" name="code" v-validate="'required|min:8'" type="text" class="form-control" placeholder="Kiểm tra email > nhập code..." autocomplete="off">
                        <span class="glyphicon glyphicon-log-in form-control-feedback"></span>
                        <span class="error">{{ errors.first("code") }}</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.password" name="password" v-validate="'required|min:6'" type="password" class="form-control" placeholder="Mật khẩu" id="password" autocomplete="off">
                        <span class="glyphicon glyphicon-lock form-control-feedback"></span>
                        <span class="error">{{ errors.first("password") }}</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.rPassword" name="rPassword" v-validate="'required|min:6|rPassword'" type="password" class="form-control" placeholder="Nhập lại mật khẩu" id="rPassword" autocomplete="off">
                        <span class="glyphicon glyphicon-log-in form-control-feedback"></span>
                        <span class="error">{{ errors.first("rPassword") }}</span>
                    </div>
                    <div class="row">
                        <div class="col-xs-8"></div>
                        <div class="col-xs-4">
                            <button type="submit" class="btn btn-primary btn-block btn-flat">Xác nhận</button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { Validator } from "vee-validate";
import axios from 'axios';

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            isEmail: null,
            isSendCode: false,
            user:{
                email:'',
                code:'',
                password:'',
                rPassword:''
            },
            dictionary: {
                vi: {
                    custom: {
                        "email":{
                            required: "Vui lòng nhập email !",
                            email: "Vui lòng nhập đúng định dạng email !",
                        },
                        "password":{
                            required: "Vui lòng nhập mật khẩu !",
                            min: "Mật khẩu tối thiểu 6 ký tự !",
                        },
                        "rPassword":{
                            required: "Vui lòng nhập mật khẩu !",
                            min: "Mật khẩu tối thiểu 6 ký tự !",
                        }
                    }
                }
            }
        }
    },
    methods: {
        async checkEmail(e){
            var data = {email: e.target.value};
            if(e.target.value){
                var check = await this.check(data)
                this.isEmail = check.result;
            }
        },
        async check(data){
            var check = await axios.get(`${this.api}/auth/check`,{
                params: data
            });
            return check.data;
        },
        sendCode(){
            this.$validator.validate().then(async valid => {
                if(valid){
                    this.$swal.fire({
                        title: 'Đang gửi code đến mail',
                        text: "Vui lòng chờ !",
                        allowOutsideClick: false
                    });
                    this.$swal.showLoading();
                    const mail = await axios.put(`${this.api}/auth/confirmMail`, {
                        email: this.user.email
                    });
                    if(mail.data.success===1){
                        this.isSendCode=true;
                        this.$swal.close();
                        this.flash(mail.data.message, 'success', {
                            timeout: 3000
                        });
                    }else {
                        this.$swal.close();
                        this.$swal(mail.data.message,"", "error");
                    }
                }
            })
            .catch(error=>{
                console.log(error);
            });
        },
        forgotPassword(){
            this.$validator.validate().then(async valid => {
                if(valid){
                    const forgotPassword = await axios.put(`${this.api}/auth/forgotpassword`, {
                        email: this.user.email,
                        code: this.user.code,
                        password: this.user.password
                    });
                    if(forgotPassword.data.success===1){
                        this.flash(forgotPassword.data.message, 'success', {
                            timeout: 3000
                        });
                        this.$router.push({name: 'login'});
                    }else this.$swal(forgotPassword.data.message,"", "error");
                }
            })
            .catch(error=>{
                console.log(error);
            });
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');

        Validator.extend('rPassword', {
            getMessage: () => 'Mật khẩu không khớp !',
            validate(){
                let password = document.getElementById("password");
                let rPassword = document.getElementById("rPassword");
                if(password.value==rPassword.value) return true;
                return false;
            }
        });
    }
}
</script>

<style>
.error{
    display: block;
    color: red;
    margin-left: 1px;
}
.content-wrapper{
    min-height: 682px;
}
@media(max-width: 768px){
    .content-wrapper{
        min-height: 100%;
    }
}
</style>